import React from 'react';
import { Link } from 'react-router-dom';

const AccountVerified = () => {
  return (
    <div style={styles.container}>
      <h2>Ti khon ca bn  c xc nhn!</h2>
      <p>By gi bn c th ng nhp vo ti khon ca mnh.</p>
      <Link to="/login" style={styles.link}>ng nhp ngay</Link>
    </div>
  );
};

const styles = {
  container: {
    textAlign: 'center',
    marginTop: '50px',
  },
  link: {
    display: 'inline-block',
    marginTop: '20px',
    padding: '10px 20px',
    background: '#007BFF',
    color: '#fff',
    borderRadius: '5px',
    textDecoration: 'none',
  },
};

export default AccountVerified;
